import {CCard, CCardBody, CRow} from "@coreui/react";
import {Link} from "react-router-dom";
import Button from "react-bootstrap/Button";
import Table from "react-bootstrap/Table";
import React, {useEffect, useState} from "react";
import plus from "../../assets/images/plus.png";
import {getCookieValue, ipAddress, tokenName} from "../../utils/Common";
import axios from "axios";

const HotelsViewMarkups = () => {
  const [markupData, setMarkupData] = useState([]);
  const [expandedRows, setExpandedRows] = useState({});

  const toggleExpand = (id) => {
    setExpandedRows((prevState) => ({
      ...prevState,
      [id]: !prevState[id]
    }));
  };
  useEffect(() => {
    getMarkupData();
  }, []);

  function getMarkupData() {
    let token = getCookieValue(tokenName);
    // console.log(token)
    axios.get(ipAddress + "admin/hotel-fetchMarkupPlans", {
      headers: {
        'Authorization': `Bearer ${token}`,
        'Content_Type': 'application-json',
      }
    }).then((response) => {
      console.log(response.data);
      setMarkupData(response.data.recordset);
    }).catch(e => {
      console.log(e)
    })
  }
  return (

          <>
            <CCard className="mb-4">
              <CCardBody>
                <CRow>
                  <div className="table-responsive px-3 ">
                    <h2 style={{textAlign: 'center'}}><b>Already Added Markups : </b></h2>
                    <hr/>
                    <div className="bg-light py-2 mb-3">
                      <Link to="/hoteladdmarkups">
                        <Button variant="primary">
                          Add New Markups
                        </Button>
                      </Link>
                      <div className="row">

                      </div>

                    </div>

                    <div className="row">

                      <br/>

                      <div style={{height: "335px"}} className="table-responsive">
                        <Table responsive hover>
                          <thead style={{position: "sticky", top: 0}}>
                          <tr>
                            <th>ID</th>
                            <th>Plan Name</th>
                            {/*<th>Carrier</th>*/}
                            {/*<th>Fare Type</th>*/}
                            {/*<th>Supplier</th>*/}
                            <th className="text-center" >Markup Type</th>
                            <th className="text-center">Markup Value</th>
                            <th className="text-center">Applied On</th>
                          </tr>
                          </thead>

                          <tbody>
                          {/*{markupData?.length > 0 && markupData.map((item, index) => (*/}
                          {markupData?.length > 0 && Object.values(
                            markupData.reduce((acc, item) => {
                              if (!acc[item.planName]) {
                                acc[item.planName] = [];
                              }
                              acc[item.planName].push(item);
                              return acc;
                            }, {})
                          ).map((group, index) => (
                            <React.Fragment key={index}>
                              <tr key={index}>
                                <td>{index + 1}</td>
                                <td className="expandable name" onClick={() => toggleExpand(group[0].planName)}
                                    style={{color: "blue", fontSize: "15px"}}><img src={plus}
                                                                                   alt="Icon"/> {group[0].planName}
                                </td>
                                {/*<td>{group[0].carrierName}</td>*/}
                                {/*<td>{group[0].fare_type}</td>*/}
                                {/*<td>{group[0].supplier}</td>*/}
                                <td className="text-center" >{group[0].markupType}</td>
                                <td className="text-center">{group[0].markupValue}</td>
                                <td className="text-center">{group[0].appliedOn}</td>
                              </tr>

                              {expandedRows[group[0].planName] &&
                                group.map((item, idx) => (

                                  <tr key={`expandedRow-${item.planName}-${idx}`}>
                                    <td colSpan="7" style={{paddingLeft: "90px"}} className="gray-row">
                                      <div className="row">

                                        <div className="col-sm-3">
                                          <b>Supplier Name :</b> {item.supplier}
                                        </div>

                                      </div>
                                    </td>
                                  </tr>
                                ))}

                            </React.Fragment>
                          ))
                          }

                          </tbody>


                        </Table>

                      </div>

                    </div>

                  </div>
                </CRow>
              </CCardBody>
            </CCard>
          </>

  )
}

export default HotelsViewMarkups
